import { useState, useEffect } from "react";
//import { useHistory } from "react-router-dom";
import axios from "axios";
import "./CryptoCurrency.css";
import "../chart/Charts";
import Card from "../card/Card";
//import Charts from "../chart/Charts";

export default function CryptoCurrency() {
  const [coins, setCoins] = useState([]);
  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(3);
  //const [idvalue, setIdvalue] = useState();

  //const history = useHistory();

  /*const pull_data = (data) => {
    console.log("pull_data " + data);
    //setIdvalue(data);
  };*/

  useEffect(() => {
    axios
      .get(
        `https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&order=market_cap_desc&per_page=${limit}&page=1&sparkline=false`
      )
      .then((res) => {
        setCoins(res.data);
        console.log(res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [limit]);

  const loadMore = () => {
    setLimit(limit + 10);
  };

  const handleChange = (e) => {
    setSearch(e.target.value);
  };

  const filteredCoins = coins.filter((coin) =>
    coin.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="row crypto">
      <div className="col-md-12 col-12">
        <form className="mt-3">
          <input
            autoFocus
            type="text"
            placeholder="Search"
            className="coin-input"
            onChange={handleChange}
          />
        </form>
        {filteredCoins.map((data, index) => {
          return (
            <Card
              name={data.name}
              key={data.id}
              id={data.id}
              image={data.image}
              current_price={data.current_price}
              price_change_percentage={data.price_change_percentage_24h}
              symbol={data.symbol}
              supply={data.circulating_supply}
            />
          );
        })}
        <button className="btn btn-secondary" onClick={loadMore}>
          Load More
        </button>
      </div>
    </div>
  );
}
