import "./Chart.css";
import { useEffect, useState } from "react";
import axios from "axios";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export default function Charts(props) {
  const [info, setInfo] = useState([]);
  //console.log("From" + props.location.state);
  const myparam = props.location.state || {};
  const idvalue = myparam !== "undefined" ? myparam : "bitcoin";
  //console.log("idvalue= " + idvalue);
  //console.log("from charts " + props.location.state);

  useEffect(() => {
    axios
      .get(
        `https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&ids=${idvalue}&order=market_cap_desc&per_page=100&page=1&sparkline=false`
      )
      .then((res) => {
        setInfo(res.data[0]);
        //console.log(res.data[0]);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [idvalue]);
  const earlier_price = info.current_price + info.price_change_24h;
  const next_price = info.current_price - info.price_change_24h;
  return (
    <div className="chart" style={{ backgroundColor: "white" }}>
      <Line
        data={{
          // x-axis label values
          labels: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          datasets: [
            {
              label: "Price Change",
              // y-axis data plotting values
              data: [earlier_price, info.current_price, next_price],
              //data:[1,2,3],
              fill: false,
              borderWidth: 4,
              backgroundColor: "rgb(255, 99, 132)",
              borderColor: "green",
              responsive: true
            }
          ]
        }}
      />
    </div>
  );
}
